}
.carousel-transition-leave,.carousel-transition-leave-to{position:absolute;top:0
}
.carousel-reverse-transition-enter,.carousel-transition-leave,.carousel-transition-leave-to{-webkit-transform:translate(-100%);transform:translate(-100%)
}
.carousel-reverse-transition-leave,.carousel-reverse-transition-leave-to{-webkit-transform:translate(100%);position:absolute;top:0;transform:translate(100%)
}
